@import url(https://fonts.googleapis.com/css?family=Anonymous+Pro);

* {
    padding: 0px;
    margin: 0px;
    /* font-family: sans-serif; */
    color: whitesmoke;
    font-family: "Anonymous Pro", monospace!important;
}
body {
    background: rgb(12, 12, 12);
    padding: 5px;
}
h3 {
    text-align: center;
}
h4 {
    border-bottom: 1px solid rgb(107, 107, 107);
    padding-bottom: 5px;
}
textarea {
    display: block;
    padding: 5px 10px;
    border-radius: 5px;
    border: 1px solid black;
    margin: auto;
    font-size: 15px;
    margin-top: center;
    color: rgb(192, 192, 192);
    width: calc(100% - 40px);
    margin: 10px;
    height: 200px;
    background: rgb(26, 26, 26);
    resize: none;
}
textarea:focus{
    outline: none;
}
.submit {
    display: block;
    background: rgb(26, 26, 26);
    border-radius: 5px;
    padding: 5px 10px;
    border: 1px solid black;
    width: calc(100% - 20px);
    margin: 10px;
    cursor: pointer;
    font-size: 16px;
}
.submit:hover {
    background: rgb(0, 0, 0);
}
.otherSecret {
    display: block;
    width: calc(100% - 40px);
    border: 1px solid black;
    background: rgb(26, 26, 26);
    margin: auto;
    padding: 5px;
    border-radius: 5px;
}
.otherSecret .body div {
    margin-top: 10px;
    margin-left: 10px;
}